import editIcon from "../images/edit.png";
import setIcon from "../images/set.png";

function EditButton(props) {
  const Button = () => {
    if (props.type === "edit") {
      return (
        <img
          onClick={props.onClick}
          className="editIcon"
          src={editIcon}
          alt="editIcon"
        ></img>
      );
    } else if (props.type === "set") {
      return (
        <img
          onClick={props.onClick}
          className="setIcon"
          src={setIcon}
          alt="setIcon"
        ></img>
      );
    } else {
      return null;
    }
  };

  return <Button />;
}

export default EditButton;
