import React from "react";

class SubNav extends React.Component {
  constructor(props) {
    super(props);
    this.state = { list: this.props.list };
    this.submenus = [];
    this.getList = this.getList.bind(this);
  }

  get code() {
    return this.props.code.split("/")[1];
  }
  get $current() {
    const currentList = this.state.list[this.code];

    return this.submenus[
      currentList.findIndex(([code]) => {
        return code === this.props.code;
      })
    ];
  }

  componentDidMount() {
    this.$current?.querySelector("a").classList.add("active");
    this.setState({
      $target: this.$current,
    });
  }

  componentDidUpdate(prevProps) {
    if (!prevProps.code.startsWith(this.props.code)) {
      if (this.props.defaultCode.startsWith(this.props.code)) {
        this.setState({ $target: this.$current });
      } else {
        this.setState({ $target: null });
      }
    }
  }
  getList() {
    const $list = [];
    const titles = this.state.list[this.code] || [];

    for (let i = 0; i < titles.length; i++) {
      $list.push(
        <li
          key={i}
          ref={($) => (this.submenus[i] = $)}
          onMouseEnter={this.onMouseEnter}
        >
          <a href={`https://admin.wingstation.co.kr${titles[i][0]}`}>
            {titles[i][1]}
          </a>
        </li>
      );
    }
    return $list;
  }
  onMouseEnter = (e) => {
    this.setState({
      $target: e.currentTarget,
    });
  };

  render() {
    const { $target } = this.state;
    const targetRect = $target?.getBoundingClientRect();

    return (
      <nav className="subNav">
        <ul
          onMouseLeave={() => {
            this.setState({ $target: this.$current });
          }}
        >
          {this.getList()}
        </ul>
        {targetRect && (
          <div
            className="ghost"
            style={{
              bottom: 0,
              left: targetRect.x,
              width: targetRect.width,
            }}
          />
        )}
      </nav>
    );
  }
}

export default SubNav;
