import React, { useEffect } from "react";

import {
  Chart,
  ArcElement,
  LineElement,
  BarElement,
  PointElement,
  BarController,
  BubbleController,
  DoughnutController,
  LineController,
  PieController,
  PolarAreaController,
  RadarController,
  ScatterController,
  CategoryScale,
  LinearScale,
  LogarithmicScale,
  RadialLinearScale,
  TimeScale,
  TimeSeriesScale,
  Decimation,
  Filler,
  Legend,
  Title,
  Tooltip,
} from "chart.js";

Chart.register(
  ArcElement,
  LineElement,
  BarElement,
  PointElement,
  BarController,
  BubbleController,
  DoughnutController,
  LineController,
  PieController,
  PolarAreaController,
  RadarController,
  ScatterController,
  CategoryScale,
  LinearScale,
  LogarithmicScale,
  RadialLinearScale,
  TimeScale,
  TimeSeriesScale,
  Decimation,
  Filler,
  Legend,
  Title,
  Tooltip
);

const data = window.value;

const canvas = (
  <canvas
    className="currentPortUsageByTime"
    style={{ width: "100%", height: "100%" }}
  ></canvas>
);

function CurrentPortUsageByTime() {
  useEffect(() => {
    const ctx = document
      .querySelector(".currentPortUsageByTime")
      .getContext("2d");

    const array = new Array(24).fill();

    const labels = array.map((value, index) => {
      let time;

      if (index < 10) {
        time = "0" + index + ":00";
      } else {
        time = index + ":00";
      }
      return time;
    });

    const chart = new Chart(ctx, {
      type: "line",
      data: {
        labels: labels,
        datasets: [
          {
            data: data,
            borderColor: "rgb(255, 206, 0)",
            pointRadius: 0,
            pointHoverRadius: 6,
            borderWidth: 2,
          },
        ],
      },
      options: {
        layout: {
          padding: {
            top: 20,
            right: 10,
            bottom: 8,
            left: 10,
          },
        },
        plugins: {
          legend: {
            display: false,
          },
        },
        responsive: false,
        maintainAspectRatio: false,
        scales: {
          x: {
            title: {
              display: true,
              text: "시간",
              font: {
                family: "Noto Sans KR",
                weight: 400,
                size: 13,
              },
              color: "rgb(107, 107, 107)",
            },
          },
          y: {
            title: {
              display: true,
              text: "포트 수",
              font: {
                family: "Noto Sans KR",
                weight: 400,
                size: 13,
              },
              color: "rgb(107, 107, 107)",
            },
            beginAtZero: true,
          },
        },
      },
    });
  });

  return (
    <div
      className="currentPortUsageByTime-container"
      style={{ width: "100%", height: "100%" }}
    >
      {canvas}
    </div>
  );
}

export default CurrentPortUsageByTime;
