import React from "react";

class MainNav extends React.Component {
  constructor(props) {
    super(props);
    this.getList = this.getList.bind(this);
    this.current = this.props.code.split("/"); // currnet[1]이 우리가 원하는값
  }

  getList() {
    const $list = [];
    const titles = [
      ["/control", "관제시스템"],
      ["/station", "스테이션 관리"],
      ["/user", "이용자 관리"],
      ["/app", "APP 관리"],
      ["/administrator", "관리자 관리"],
    ];

    for (let i = 0; i < titles.length; i++) {
      $list.push(
        <li
          key={i}
          className={
            this.props.hoveringCode === titles[i][0] ? "active" : undefined
          }
        >
          <a
            className="nav"
            href={`https://admin.wingstation.co.kr${titles[i][0]}`}
            style={{
              backgroundColor:
                titles[i][0] === "/" + this.current[1]
                  ? "rgb(255, 206, 0)"
                  : "initial",
            }}
            onMouseEnter={() => this.props.onMainNavMouseEnter(titles[i][0])}
          >
            {titles[i][1]}
          </a>
        </li>
      );
    }

    return $list;
  }

  render() {
    return (
      <nav className="mainNav">
        <ul>{this.getList()}</ul>
      </nav>
    );
  }
}

export default MainNav;
